@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Koulen&family=Quicksand&family=Roboto&display=swap');

body {
    background: url(components/images/bg.gif) 100% no-repeat;
    background-size: cover;
}

.component {
    font-family: 'Courier New', Courier, monospace;
    max-width: 550px;
    margin: auto;
    text-align: center;
    background: url(components/images/gradient.gif) 100% no-repeat;
    background-size: cover;
    border-radius: 15px;
    border: solid #4676D7
}

input {
    padding: 5px;
    margin: 5px;
}

p {
    overflow: hidden;
}

.meme {
    font-family: 'Koulen', cursive;
    font-weight: bold;
    font-size: 40px;
    text-transform: uppercase;
    color: white;
    text-shadow: 
        1px 0 1px #000,
        -1px 0 1px #000,
        0 1px 1px #000,
        0 -1px 1px #000
    ;
    position: relative;
    height: 400px
}

.fst {
    position: absolute;
    top: 0px;
    left: 40px;
    right: 40px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.snd {
    position: absolute;
    bottom: 0px;
    left: 40px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    right: 40px;
}

button {
    /* Сброс appearance (Оно используется для стилизации,
    характерной для конкретной платформы.
    Внешний вид кнопки при таком подходе основан на стилях,
     применяемых в операционной системе пользователя.)*/
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    /* Далее, прежде чем мы перейдём, собственно,
     к стилизации, нам нужно сбросить ещё некоторые свойства.
    Это — border, border-radius, background. */
    border: 0;
    border-radius: 0;
    background: #ccc;
}
/* Теперь, стилизация */
button {
    appearance: none;
    margin: 5px;
    border: 0;
    border-radius: 5px;
    background: #4676D7;
    color: #fff;
    font-size: 35px;
    padding: 8px 16px;
    font-size: 16px;
}

button:hover {
    background: #588df6;
}

footer {
    margin-top: 70px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    text-align: center;
    max-width: 465px;
    margin: auto
}

.info {
    margin-top: 40px;
    color: rgb(183, 183, 183)
}

h1 {
    font-size: 60px;
    margin: 15px 0 0 25px;
    font-family: 'Bebas Neue', cursive;
    text-align: left;
    color: rgba(255, 255, 255, 0.9)
}
